import React, { useEffect, useState } from "react";
import ListPage from "../components/ListPage";
import searchImg from "../assets/search.svg";
import styles from "./CourseListPage.module.css";
import CourseItem from "../components/CourseItem";
import { getDatas } from "../api/firebase";
import Warn from "../components/Warn";

let listItems;
function CourseListPage(props) {
  const [items, setItems] = useState([]);
  const [keyword, setkeyword] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const handlekeywordChange = (e) => {
    //    state .
    setkeyword(e.target.value);
  };
  const etargetArr = [];
  const handleSubmit = (e) => {
    e.preventDefault();
    //     listItems 

    //    title       .
    const searchItems = listItems.filter(function (item) {
      return item.title.includes(keyword);
    });
    //   items state set .
    setItems(searchItems);
    setItems(listItems.filter(({ title }) => title.includes(keyword)));
  };

  const handleLoad = async () => {
    setIsLoading(true);
    //  courses   .
    const resultData = await getDatas("coureses");
    //    .
    //   
    listItems = resultData;
    // items state set .
    setItems(resultData);
    setIsLoading(false);
  };
  useEffect(() => {
    handleLoad();
  }, []);

  return (
    <ListPage back="catalog">
      <form className={styles.form} onSubmit={handleSubmit}>
        <input onChange={handlekeywordChange} />
        <button>
          <img src={searchImg} />
        </button>
      </form>
      <p className={styles.count}> {items.length} </p>
      {items.length === 0 && !isLoading ? (
        <Warn
          className={styles.emptyList}
          title="   ."
          description="       ."
        />
      ) : (
        <div className={styles.courseList}>
          {items.map((course) => {
            return <CourseItem key={course.docId} course={course} />;
          })}
        </div>
      )}
    </ListPage>
  );
}

export default CourseListPage;
